#contact {
  width: 170px;
  height: 60px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  border-right: 1px solid gray;
  margin-top: 52px;
  margin-left: 85px;
}
#contact-heading {
  width: 100%;
  height: 60px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
#contact-name {
  width: 150px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5vw;
  font-family: Acme;
  background-image: linear-gradient(
    45deg,
    #0013e5 16.666%,
    #008c7f 33.333%,
    #00377f 50%,
    #5d007f 66.666%,
    #ea007f 83.333%
  );
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
